var that;
export default {
	data() {
		return {
			intervalID: 0,
			havetime: 60,
			strname: '',
			zhanghao: '',
			yanzhengmacode: '',
			xuehao: '',
			zhengjianhaoma: '',
			diyu: '',
			mima: '',
			strtime: '获取验证码',
			state: 0,
			quyulist: ['西安交通大学', '西北工业大学', '西安音乐学院'],
			wangsulist: ['100M', '200M', '500M'],
			wangsu: '100M',
			wangshuliang: '1',
			iptvshuliang: '0'

		}
	},
	onLoad: function(e) {
		that = this;
	},
	onShow: function(res) {

	},
	onUnload() {
		clearInterval(that.intervalID)
	},
	methods: {
		quxiaozhuce: function() {
			uni.navigateBack({
				delta: 1
			})
		},
		selectwangsu: function() {
			uni.showActionSheet({
				itemList: that.wangsulist,
				itemColor: '#838383',
				success: function(res) {
					that.wangsu = that.wangsulist[res.tapIndex]
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},
		selectquyu: function() {
			uni.showActionSheet({
				itemList: that.quyulist,
				itemColor: '#838383',
				success: function(res) {
					that.diyu = that.quyulist[res.tapIndex]
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		lijizhuce: function() {
			if (!that.strname) {
				uni.showToast({
					title: '请输入姓名'
				})

				return
			}
			if (!that.zhanghao) {
				uni.showToast({
					title: '请输入账号'
				})

				return
			}
			if (!that.yanzhengmacode) {
				uni.showToast({
					title: '请输入验证码'
				})

				return
			}
			if (!that.xuehao) {
				uni.showToast({
					title: '请输入学号'
				})

				return
			}
			if (!that.zhengjianhaoma) {
				uni.showToast({
					title: '请输入证件号码'
				})

				return
			}
			if (!that.diyu) {
				uni.showToast({
					title: '请输入所在地域'
				})

				return
			}
			if (!that.mima) {
				uni.showToast({
					title: '请输入登陆密码'
				})

				return
			}
			if (!that.mima) {
				uni.showToast({
					title: '请输入登陆密码'
				})

				return
			}
			if (that.wangshuliang == 0) {
				uni.showToast({
					title: '请输入数量'
				})

				return
			}
			if (that.iptvshuliang == 0) {
				uni.showToast({
					title: '请输入数量'
				})

				return
			}
			uni.navigateBack({
				delta: 1
			})
		},

		yanzhengma: function() {
			console.log(that.zhanghao)
			if (that.state == 1) {
				return
			}
			if (!that.zhanghao) {
				uni.showToast({
					title: '请输入账号'
				})
				return
			}
			that.state = 1;
			that.intervalID = setInterval(function() {
				that.havetime--;
				if (that.havetime > 0) {
					that.strtime = that.havetime + 'S后获取'
				} else {
					clearInterval(that.intervalID)
					that.state = 0;
					that.havetime = 60;
					that.strtime = '获取验证码'
				}
			}, 1000)

		},

	}

}
